import React from 'react'
import { Slider, Handles } from 'react-compound-slider';
import './slider.css';
import PropTypes from 'prop-types';

export function Handle({ // your handle component
    handle: { id, value, percent }, 
    getHandleProps
}) {
    return (
        <div
        style={{
            left: `${percent}%`,
        }}
        className="PHSlider__handle"
        {...getHandleProps(id)}
        >
            <div className="PHSlider__handle-text">
                {value} %
            </div>
        </div>
    )
}

class PHSlider extends React.Component {

    handleOnChange = (values) => {
        this.props.onChange(values[0]);
    }

    render() {
        return (
            <Slider
                className="PHSlider"
                domain={[0, 8]}
                values={[this.props.value]}
                step={0.5}
                onChange={this.handleOnChange}
            >
                <div className="PHSlider__rail" />

                <Handles>
                    {({ handles, getHandleProps }) => (
                        <div className="slider-handles">
                            {handles.map(handle => (
                                <Handle
                                    key={handle.id}
                                    handle={handle}
                                    getHandleProps={getHandleProps}
                                />
                            ))}
                        </div>
                    )}
                </Handles>

            </Slider>
        );
    }
}

PHSlider.propTypes = {
    value: PropTypes.number,
    onChange: PropTypes.func,
};

PHSlider.defaultProps = {
    value: 0,
    onChange: () => {}
}

export default PHSlider;