import React from 'react';
//import logo from './logo.svg';
import './App.css';
import PropTypes from 'prop-types';
import PHSlider from './slider';

const operations = {
  SELL: 'venta',
  RENT: 'renta',
};

const payment = {
  CREDIT: 'credit',
  CASH: 'cash',
}

class App extends React.Component 
{
  constructor(props) {
      super(props);

      this.state = {
          operation: operations.SELL,
          value: 600000,
          payment: payment.CASH,
          commission: 4,
      }
  }

  setOperation = (operation) => {
      this.setState({operation});
  }

  operationClassName = (operation) => {
      return this.state.operation === operation ?
      "PHCalc__op active" :
      "PHCalc__op";
  }

  handleValueChange = (evt) => {
      console.log(evt.target.value, this.moneyToFloat(evt.target.value));
      let value = this.moneyToFloat(evt.target.value);
      this.setState({value});
  }

  handlePaymentOption = (payment) => {
      this.setState({payment})
  }

  handleCommission = (commission) => {
      this.setState({commission});
  }

  moneyToFloat(numberString) {
      if (numberString === '') {
          return 0;
      }

      return parseFloat(
          Math.round(
              parseFloat(numberString.replace(/,/g, '')) * 100
          ) / 100
      ).toFixed(2);
  }

  floatToMoney(number) {
      return new Intl.NumberFormat('es-MX', {currency: 'MXN'}).format(number);
  }

  getResultOperation = () => {
      
      let product_cost = this.state.payment === payment.CREDIT ?
          this.props.credit_cost :
          this.props.cash_cost;
          
      let result = 0;

      if (this.state.operation === operations.SELL) {
          result = this.state.value * (this.state.commission / 100) - product_cost;
      } else {
          result = this.state.value - product_cost;
      }

      return result;
  }

  render() {
      let formattedValue = this.floatToMoney(this.state.value);
      let formattedResult = this.floatToMoney(this.getResultOperation());

      return (
          <div className="PHCalc">
              <header className="PHCalc__header">{this.props.encabezado}</header>
              <div className="PHCalc__quest">
                  <div className="PHCalc__text">{this.props.preg_operacion}</div>
                  <div className="PHCalc__widget PHCalc__operation-type">
                  <button
                      type="button"
                      className={this.operationClassName(operations.RENT)}
                      onClick={() => this.setOperation(operations.RENT)}
                  >
                      {this.props.rentar}
                  </button>
                  <button
                      type="button"
                      className={this.operationClassName(operations.SELL)}
                      onClick={() => this.setOperation(operations.SELL)}
                  >
                      {this.props.vender}
                  </button>
                  </div>
              </div>

              <div className="PHCalc__quest">
                  <div className="PHCalc__text">{ this.state.operation === operations.SELL ? this.props.preg_valor : this.props.preg_precio_estimado}</div>
                  <div className="PHCalc__widget PHCalc__property-value">
                      <div className="input-group">
                          <div className="input-group-prepend">
                              <div className="input-group-text">$</div>
                          </div>
                          <input type="text" className="form-control" onChange={this.handleValueChange} value={formattedValue} placeholder="600,000.00" />
                      </div>
                  </div>
              </div>

              <div className="PHCalc__quest">
                  <div className="PHCalc__text">{this.props.preg_opcion}</div>
                  <div className="PHCalc__widget PHCalc__payment-option">
                      <div className="form-check">
                          <label className="form-check-label">
                              $ {this.floatToMoney(this.props.credit_cost)} a meses sin intereses
                              <input
                                  type="checkbox"
                                  className="form-check-input"
                                  value={payment.CREDIT}
                                  checked={this.state.payment === payment.CREDIT}
                                  onChange={() => this.handlePaymentOption(payment.CREDIT)}
                              />
                              <span className="form-check-checkmark"></span>
                          </label>
                      </div>
                      <div className="form-check">
                          <label className="form-check-label">
                              $ {this.floatToMoney(this.props.cash_cost)} de contado
                              <input
                                  type="checkbox"
                                  className="form-check-input"
                                  value={payment.CASH}
                                  checked={this.state.payment === payment.CASH}
                                  onChange={() => this.handlePaymentOption(payment.CASH)}
                              />
                              <span className="form-check-checkmark"></span>
                          </label>
                      </div>
                  </div>
              </div>
              
              { this.state.operation === operations.SELL ?
                (
                  <div className="PHCalc__quest">
                    <div className="PHCalc__text">{this.props.preg_comision}</div>
                    <div className="PHCalc__widget PHCalc__slider">
                      <PHSlider
                          value={3}
                          onChange={this.handleCommission}
                      />
                    </div>
                  </div>
                ) : (
                  <div className="PHCalc__widget PHCalc__spacer"></div>
                )
              }
              
              
              <div className="PHCalc__footer-text">{this.props.texto_footer}</div>
              <div className="PHCalc__result">
                  <div className="PHCalc__result-text">{this.props.ahorro}</div>
                  <div className="PHCalc__result-number">$ {formattedResult}</div>
              </div>
          </div>
      );
  }
}

App.propTypes = {
  encabezado: PropTypes.string,
  rentar: PropTypes.string,
  vender: PropTypes.string,
  preg_operacion: PropTypes.string,
  preg_valor: PropTypes.string,
  preg_precio_estimado: PropTypes.string,
  preg_opcion: PropTypes.string,
  preg_comision: PropTypes.string,
  texto_footer: PropTypes.string,
  ahorro: PropTypes.string,
  credit_cost: PropTypes.number,
  cash_cost: PropTypes.number,
};

App.defaultProps = {
  encabezado: "¿Qué te regalarías con este ahorro?",
  rentar: "Rentar",
  vender: "Vender",
  preg_operacion: "¿Qué necesitas?",
  preg_valor: "Valor de mi propiedad:",
  preg_precio_estimado: "Precio de renta estimado:",
  preg_opcion: "Opciones de pago",
  preg_comision: "Comisión estimada",
  texto_footer: "El beneficio económico es solo uno de los mucho que obtiene al contratar los servicios de PropertyHub",
  ahorro: "Ahorro",
  credit_cost: 56000,
  cash_cost: 49900,
};

export default App;
